@font-face {
    font-family: 'HSSanTokki20-Regular';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2405@1.0/HSSanTokki20-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.home {
    width: 100%;

    .ttoki {
        font-family: 'HSSanTokki20-Regular';
        font-weight: 400;
    }

    .contentswrap {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        width: 100%;

        .left {
            width: 82%;
            display: flex;
            flex-direction: column;
            gap: 2.1875rem;
            align-self: flex-end;

            img {
                width: 100%;
            }

            .leftbox_title {
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                span {
                    font-size: 12.5rem;
                    font-weight: 400;
                }

                .uptitle {
                    line-height: 1;
                }
            }

            .prjbox,
            .prjbox2 {
                display: flex;
                justify-content: space-between;
                border: #000 1px solid;
                background-color: #F4E9E1;
                font-size: 1.8rem;
                font-weight: 500;
                transition: transform 0.4s ease, box-shadow 0.4s ease;
                transform-style: preserve-3d;
                transform-origin: center center;
                cursor: pointer;

                span {
                    width: 33.333%;
                    text-align: center;
                }

                .prjboxtitle {
                    text-align: left;
                }

                .prjboxnum {
                    text-align: end;
                }

                &:hover {
                    background-color: #2835F8;
                    color: #fff;
                    border: #2835F8 solid 1px;
                    transform: perspective(800px) rotateX(0deg) scale(1.01);
                    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
                }
            }

            .prjbox2 {
                &:hover {
                    background-color: #000;
                    color: #fff;
                    border-color: #000;
                    transform: perspective(800px) rotateX(0deg) scale(1.01);
                    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
                }
            }

            .leftboxsize1 {
                width: 100%;
                height: 770px;
            }

            .leftbox {
                border-radius: 20px;
                padding: 2.1875rem;
            }

            .box1 {
                border: 1px solid #000;

                span {
                    font-family: 'HSSanTokki20-Regular';
                    font-weight: 500;
                }

                .box1_inner {
                    display: flex;
                    flex-direction: column;
                    span {
                        line-height: 1.8rem;
                        font-family: 'pretandard';
                        font-weight: 400;
                        font-size: 1rem;
                    }

                }

                .mainT1 {
                    padding-top: 19rem;
                }

                .box1_bottom {
                    display: flex;
                    justify-content: space-between;
                    padding-top: 2rem;
                }
            }

            .box2 {
                background-color: #FF3D00;
                color: #fff;
            }

            .box3 {
                border: 1px solid #000;
                border-radius: 20px;
                display: flex;
                flex-direction: column;
                text-align: left;

                .box3_inner {
                    display: flex;
                    gap: 20px;
                    padding-left: 2.1875rem;
                    padding-right: 2.1875rem;
                    padding-top: 2.1rem;
                    padding-bottom:2.1rem;;
                    

                    .box3title2{
                        font-size:2rem ;
                         width: 30%;
                    }
                    .box3title {
                        width: 30%;
                        font-size: 1.5rem;
                        font-weight: 600;
                        line-height: 2.5rem;
                    }

                    .box3_inner_box {
                        display: flex;
                        flex-direction: column;
                        font-size: 1.2rem;
                        justify-content: center;

                        span {
                            // padding-bottom: 15px;
                            line-height: 2.4rem;
                            font-weight: 400;
                        }
                    }
                }
            }

            .box4 {
                background-color: #2835F8;
                color: #fff;
            }

            .box11 {
                background-color: #000;
                color: #fff;
            }

            .box14 {
                border: 1px #000 solid;

                p {
                    font-size: 1.25rem;
                    line-height: 40px;
                    font-family: 'pretandard';
                    font-weight: 400;

                    b {
                        font-family: 'HSSanTokki20-Regular';
                        font-weight: 400;
                    }
                }
            }
        }

        .sidebar {
            padding: 2.1875rem 0;
            width: 15.62%;
            display: flex;
            flex-direction: column;
            gap: 2.1875rem;
            height: 100%;
            flex-shrink: 0;
            position: fixed;
            top: 0;
            left: 2.1875rem;

            .sidebarbox {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                padding: 1.25rem;
                color: #fff;
                border-radius: 20px;
                font-weight: 400;
                font-size: 1.5rem;
                flex-grow: 0;
                flex-basis: 105px;
                transition: all 0.5s ease;
                // height: auto;
                cursor: pointer;

                &.active {
                    flex-grow: 1;
                    height: 100%;
                }
            }

            .num1 {
                background-color: #fff;
                color: #000;
            }

            .num2 {
                background-color: #FF3D00;
            }

            .num3 {
                background-color: #2835F8;
            }

            .num4 {
                background-color: #000;
            }
        }
    }
}