.project {
    .contentswrap {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        width: 100%;

        .right {
            // width: 82%;
            // height: max-content;
            // display: flex;
            // flex-direction: column;
            // gap: 2.1875rem;
            width: 82%;
            display: flex;
            flex-direction: column;
            gap: 2.1875rem;
            align-self: flex-end;

            .rightbox {
                border-radius: 20px;
                padding: 2.1875rem;
            }

            .box1 {
                width: 100%;
                height: 300px;
                background-color: #2835F8;
                color: #fff;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;

                span {
                    font-size: 150px;
                    font-weight: 500;
                    padding-bottom: 0.625rem;
                }

                ul {
                    display: flex;
                    gap: 0.625rem;
                    align-items: center;

                    .line {
                        font-size: 0.625rem;

                    }

                    li {
                        font-weight: 200;
                        font-size: 1.25rem;
                    }

                }
            }

            .box2 {
                width: 100%;
                height: 760px;
                overflow: hidden;
                border-radius: 20px;

                video {
                    // width: 100%;
                    height: 100%;
                }
            }

            .box3 {
                display: flex;
                flex-direction: column;
                gap: 0.625rem;
                padding-left: 0.5rem;

                span {
                    font-size: 1.125rem;
                    font-weight: 400;
                }

                .box3T {
                    font-size: 2rem;
                    font-weight: 500;
                    margin-bottom: 0.625rem;
                }
            }

            .box4 {
                display: flex;
                justify-content: space-between;

                button {
                    width: 350px;
                    height: 60px;
                    text-decoration: underline;
                    font-size: 1.5rem;
                    font-weight: 400;
                    background-color: transparent;
                    border: none;
                    padding: 0.625rem;
                }

                .box4_btn1 {
                    text-align: left;
                }

                .box4_btn3 {
                    text-align: right;
                }

            }
        }
        .sidebar {
            padding: 2.1875rem 0;
            width: 15.62%;
            display: flex;
            flex-direction: column;
            gap: 2.1875rem;
            height: 100%;
            flex-shrink: 0;
            position: fixed;
            top: 0;
            left: 2.1875rem;

            .sidebarbox {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                padding: 1.25rem;
                color: #fff;
                border-radius: 20px;
                font-weight: 400;
                font-size: 1.5rem;
                flex-grow: 0;
                flex-basis: 105px;
                transition: all 0.5s ease;
                // height: auto;
                cursor: pointer;
                &.active {
                    flex-grow: 1;
                    height: 100%;
                    background-color: #2835F8;
                    color: #fff;
                    border: #2835F8 1px solid;
                }
            }

            // .num1 {
            //     background-color: #fff;
            //     color: #000;
            // }

            // .num2 {
            //     background-color: #FF3D00;
            // }

            // .num3 {
            //     background-color: #2835F8;
            // }

            // .num4 {
            //     background-color: #000;
            // }

            .num1, .num2, .num3, .num4, .num5 {
                background-color:#F4E9E1;
                color:#000;
                border: #000 1px solid;

            
            }
        }
    }
}