@font-face {
    font-family: 'HSSanTokki20-Regular';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2405@1.0/HSSanTokki20-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

* {
    margin: 0;
    padding: 0;
    font-family: pretendard;
    box-sizing: border-box;
}

ol,
ul,
li {
    list-style: none;
    text-decoration: none;
}

button {
    cursor: pointer;
}

.App {
    padding: 0 35px;
    background-color: #F4E9E1;
    padding-top: 35px;
    width: 100%;
}

html {
    font-size: 16px;
}

.top {
    display: flex;
    width: 82%;
    margin-left: auto;
    justify-content: space-between;
}

.text-bounce {
    width: 50%;
    cursor: pointer;

}

.text-bounce img {
    width: 200px;transition: all ease-in-out 0.2s;
}

.text-bounce img:hover {
    transform-origin: center center;
    transform: rotate(-15deg);
}

.btncover {
    width: 50%;
    height: 60px;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2.1875rem;
}

.btn1 {
    border-radius: 10px;
    width: 200px;
    height: 60px;
    font-weight: 600;
    font-size: 1.8rem;
    border: 1px solid #000;
    background-color: #F4E9E1;
    transition: all ease-in 0.3s;
}
.btn1:hover{
    border: 1px solid #000;
    background-color: #000;
    color:  #F4E9E1;
}

.footer {
    margin-top: 35px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.box15 {
    width: 82%;
    border: #000 1px solid;
    background-color: #000000;
    text-align: center;
    font-size: 1.8rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    color: #F4E9E1;
    gap: 0.625rem;
}

.box15>img {
    width: 50px;
}

.box15>span {
    line-height: 45px;
}

.bottomT {
    width: 82%;
    text-align: center;
    margin-top: 4.375rem;
    margin-bottom: 4.375rem;
    font-size: 0.875rem;
}

.footerbox {
    border-radius: 20px;
    padding: 2.1875rem;

}